var en={translations:{'Navigation 1':'White Paper','Navigation 2':'Developers','Navigation 3':'Core Tech','Navigation 4':'Team','Navigation 5':'Contact','Hero Title':'Smart Ethernet Protocol','Hero Description':'A new blockchain protocol using programmable packets to enhance security, privacy and net neutrality','CTA Prompt':'Get early access to the SDK','CTA 1':'Join Community','CTA 2':'View Sample Code','M1 Title':'Core Technology','M1 Description':'The Marconi Protocol is designed down to the ethernet level and powers a <1 /> decentralized networking stack that enhances privacy, security, net neutrality and <3 /> upgradability.','M1 Sub1 Title':'Ethernet-Level Encryption','M1 Sub1 Description':'Secure communication pipes established at Layer 2 of the OSI model provide packet-level encryption that’s more secure than just using SSL and TLS','M1 Sub2 Title':'Programmable Packets','M1 Sub2 Description':'Decentralized apps with direct access to network packets provide network administration and security functionality without expensive custom hardware','M1 Sub3 Title':'Branch Chains','M1 Sub3 Description':'Branchable blockchains enable the creation of new decentralized networks without traditional infrastructure that’s controlled by just a few entities in any given region','Learn More Prompt':'Learn more about the Marconi Platform','Learn More CTA':'Read White Paper','M2 Title':'Use Cases','M2 Sub1 Title':'Token to Blockchain Migration','M2 Sub1 Description':'Migrate token projects (i.e. ERC20) to their own chain where the tokens now provide utility by powering the blockchain','M2 Sub2 Title':'Virtualized Blockchain','M2 Sub2 Description':'Jump start new blockchains that leverage the Marconi Network for secure block creation and persistence','M2 Sub3 Title':'Dynamic Networks','M2 Sub3 Description':'Rapidly deploy dynamic networks in battlefields and disaster recovery areas. Record network activity on a ledger for after action reviews','M2 Sub4 Title':'Security Apps','M2 Sub4 Description':'Create decentralized security apps like Anti-Phishing, Anti-Malware, Intrusion Prevention Systems and dVPNs','M2 Sub5 Title':'Network Admin Apps','M2 Sub5 Description':'Create administrative applications for network load balancing, changing network topology and IOT device management','M2 Sub6 Title':'IOT Device Management','M2 Sub6 Description':'Develop apps that address security, privacy and connectivity issues for IOT device management','M3 Title':'Developers','M3 Header 1':'applications','M3 Description 1':'Write decentralized applications using Marconi Script, a Turing-complete language with access to network packets.','M3 Description 2':'Decentralized Apps can be submitted to the global chain or branch chains for public and private networks.','M3 Header 2':'library','M3 Description 3':'APIs enable traffic routing, packet analysis, pattern recognition, branch chain creation and token migration.','M3 CTA Prompt':'Get early access to the SDK','M3 CTA 1':'Join Community','M3 CTA 2':'Request Git Access','M3 Header 3-1':'sample','M3 Header 3-2':'code','M3 Tab1 Title':'Network Creation','M3 Tab1 Description':'Spin up a new blockchain in just a few lines of code','M3 Tab2 Title':'Network Admin','M3 Tab2 Description':'Establish policies to determine what type of traffic is permitted on the network','M3 Tab3 Title':'Anti-Phishing','M3 Tab3 Description':'Analyze network traffic for phishing attacks such as masquerading URLs with unicode characters','Team Bio Title':'Core Team','Jong Title':'Blockchain','Jong Description':'Bitcoin miner, blockchain developer & investor since 2011. Founder of HashLayer (2014-15) - one of the first multi-blockchain explorers. Led teams at Google that built networking technologies. CS from USC.','Darryl Title':'Networking','Darryl Description':'Led engineering teams that optimize networking and search systems at Google. Also developed software defined radios and network drivers at Qualcomm. CS from USC.','Albert Title':'Security','Albert Description':'Developed security systems and cryptographic services to secure consumer and internal data at CreditKarma. Software Engineering - UWaterloo.','Di Title':'Platform','Di Description':'Led engineering teams that created big data platforms at Credit Karma and Service Now. Previously, software engineer at tech giants like Microsoft and Intel. Software Engineering - UWaterloo.','Kyle Title':'Infrastructure','Kyle Description':'Built data center infrastructure at Google for scaling high density and wireless networks. CS at Gavilan, BA from UC Berkeley.','Peter Title':'Operations','Peter Description':'Blockchain investor since 2014. Executive and management roles at startups and corporations like Zynga and Microsoft. Computer Engineering from UWaterloo, MBA from Wharton.','Chris Title':'Product & SDK','Chris Description':'Developed APIs and SDKs at Apple for Mac OS X and Objective-C. Led product teams and launched apps at startups like Dropbox. CS from UCSD, MBA from Wharton.','Advisors Title':'Advisors','George Title':'CEO of WeTrust','George Description':'Co-Founder of WeTrust - blockchain insurance platform backed by Vitalik Buterin, Bo Shen and Fenbushi Capital. Previously at Google and McKinsey. MS from Stanford.','Matt Title':'Co-Founder of Origin','Matt Description':'Co-Founder of Origin - protocol for creating sharing economy marketplaces using Ethereum and IPFS. Backed by Pantera Capital. Previously at Google. EE and MS from Stanford.','Jay Title':'CEO of Spoke','Jay Description':'Co-founded Appurify - mobile platform (acquired by Google) and Spoke - workplace AI (backed by Greylock and Accel). Accomplished researcher in microprocessor reliability. EE from IIT, PhD from UI Urbana Champaign.','Joyce Title':'ML & IOT Researcher','Joyce Description':'Pioneering researcher and author in machine learning comfort prediction using IOT. Previously, researcher in smart grids at Berkeley Labs. Engineering from UWaterloo, PhD from UCBerkeley.','Glenn Title':'Serial Entrepreneur','Glenn Description':'Co-Founded PDI (studio that produced the <1>Shrek</1> and <3>Madagascar</3> series) and VanEdge Capital. CEO of DreamWorks Interactive. Advisor for corporations including Zynga & Citigroup.','Stefan Title':'Finance','Stefan Description':'Senior VP at Plenty ($226M raised – Softbank), ProsperWorks  ($86M raised – DHVC) and Harvest Power ($224M raised – Kleiner Perkins). Econ at UCBerkeley, MBA from Wharton','Tom Title':'CSO of BitGo','Tom Description':'Security expert and Chief Security Officer at Bitgo - Leading blockchain security platform and a pioneer of multi-sig technology. Previously, CSO at Neustar and DocuSign. MPA from Harvard.','Join Modal Title':'Get early access to the Marconi SDK and the latest product updates','Join Modal Name':'Full name*','Join Modal Organization':'Organization','Join Modal Description':'Why are you interested?','Thanks Modal title':'Thank You!','Thanks Modal welcome':'Welcome to the Marconi community','GDPR Confirm':'I want to receive product updates over email.','GDPR Text':'I understand the Marconi Foundation will store my information to send these updates. I have read the <1>Privacy Policy</1> and can opt out of receiving products or product updates by selecting an `unsubscribe` option through an e-mail message. Upon opting out, personal information will not be collected and any previously collected personal information will be deleted.','White Paper Link':'https://docsend.com/view/5zragmb'}};

